@import url(https://fonts.googleapis.com/css?family=Comfortaa|Comic+Neue&display=swap);
* {
  font-family: 'Comic Neue', cursive, sans-serif;
}

.vertical-align {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.subheading {
  color: grey;
  font-size: 1rem;
}

.heading {
  font-family: "Comfortaa";
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.loading {
  position: absolute;
  /* top: 0; */
  left: 50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.6);
  display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.err-txt {
  font-size: 0.8rem;
  color: red;
}

.small-txt {
  font-size: 0.8rem;
  color: grey;
}

.main-div-width {
  width: 80%;
  min-height: 100vh;
  margin: 0 auto;
}

.gradientbg {
  background-color: #fd8f67;
  background: -webkit-gradient(linear, left bottom, left top, from(#fd8f67), to(#d463a3));
  background: linear-gradient(0deg, #fd8f67, #d463a3);
}
/*# sourceMappingURL=App.css.map */
.auth-div {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.02);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.btn {
  outline: none;
  border: none;
  background: none;
  padding: 5px 15px;
  margin: 1rem;
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.btn:focus {
  outline: none;
  border: none;
}

.btn:hover {
  color: black;
}

.border {
  border: solid 1px rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}
