.home-page-div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
/*# sourceMappingURL=HomePage.css.map */
.profile-img-div {
  border: 1.5px dashed #e4e46b;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  background-color: rgba(173, 154, 155, 0.7);
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.profile-img-div:hover {
  background: #0B486B;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#ce8b6a), to(#3078a1));
  background: linear-gradient(to right, #ce8b6a, #3078a1);
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.img-input {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: black;
  opacity: 0;
  cursor: pointer;
}

.profile-img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
  color: white;
  z-index: 0;
  pointer-events: none;
}

.profile-btn {
  outline: none;
  border: none;
  background: none;
  padding: 5px 15px;
  margin: 1rem;
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  margin: 0;
  text-align: center;
  color: white;
}

.profile-btn:focus {
  outline: none;
  border: none;
}

.profile-btn:hover {
  color: black;
}
